.checkout-page {
  padding: 20px;
}
.checkout-card {
  background: white;
  padding: 20px;
  max-width: 500px;
  margin: auto;
  border-radius: 8px;
  box-shadow: 0 0 8px rgba(0,0,0,0.1);
  text-align: center;
}
.checkout-card img {
  width: 150px;
  height: 150px;
  object-fit: contain;
  margin-bottom: 15px;
}
