.custom-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #111;
  color: white;
}
.logo {
  font-size: 20px;
  font-weight: bold;
  color: #f5c518;
}
.nav-links {
  display: flex;
  gap: 15px;
}
.nav-links a {
  color: white;
  text-decoration: none;
}
.hamburger {
  display: none;
  font-size: 20px;
  cursor: pointer;
}
@media (max-width: 768px) {
  .nav-links {
    display: none;
    flex-direction: column;
    background: #222;
    position: absolute;
    top: 60px;
    right: 0;
    padding: 10px;
  }
  .nav-links.active {
    display: flex;
  }
  .hamburger {
    display: block;
  }
}